import React from "react"
import {useForm} from "react-hook-form";
import styles from "./PersonDataEdit.module.css";


const PersonDataEdit = (props) => {

    const {register, handleSubmit, formState: {errors}} = useForm({
        mode: "onChange"
    });

    const onSubmit = (data) => {
        console.log(data)
        props.SaveProfile({
            fullName: data.fullName,
            aboutMe: data.aboutMe,
            lookingForAJob:data.lookingForAJob,
            lookingForAJobDescription: data.lookingForAJobDescription,
            contacts: {
                facebook: data.facebook,
                github: data.github,
                instagram: data.instagram,
                mainLink: data.mainLink,
                twitter: data.twitter,
                vk: data.vk,
                website: data.website,
                youtube: data.youtube
            }
        }).then(response=>{
            props.SetEdit(false)
        }).catch(()=>[

        ])




    }



    const input = (el, type, message=true) => {
        let nameI;
        switch (el) {
            case"lookingForAJobDescription":
                nameI = 'Мои умения?*'
                break

            case"aboutMe":
                nameI = 'О Себе *'
                break
            case"fullName":
                nameI = 'Имя *'
                break
            case"lookingForAJob":
                nameI = 'В поиске работы?   '
                break

            default:
                nameI = el

        }
        return (
            <label className={styles.label}>
                <div className={styles.body}>
                    <p className={styles.text}>

                        {nameI}

                    </p>
                    <input className={styles.input} defaultValue={props.profile[el]?
                        props.profile[el]:
                        props.profile.contacts[el]}
                           type={type}  {...register(`${el}`, {
                        required: message,

                    })} />
                </div>
                <div className={styles.message}>{errors[`${el}`] && <p>{errors[`${el}`].message}</p>}</div>
            </label>

        )
    }

    return (
        <form className={styles.form} onSubmit={handleSubmit(onSubmit)}>
            {Object.keys(props.profile).map((el) => {
                if (el !== 'photos')
                    if (el !== 'contacts')
                        return (
                            <div key={el}>
                                {el === 'lookingForAJob' ? '' :
                                    input(el, 'textarea', 'обязательно поле!')}
                            </div>
                        )
            })}
            <label className={styles.label}>
                <div className={styles.body}>
                    <p className={styles.text}>
                        Нужна работа?
                    </p>
                    <input className={styles.input} defaultChecked={props.profile.lookingForAJob}
                           type={'checkbox'}  {...register(`lookingForAJob`)} />
                </div>
            </label>
            <p>Contacts</p>
            {Object.keys(props.profile.contacts).map((el) => {
                return (
                    <div key={el}>

                        {props.errorEdit.map((er) => {
                            if (er.toUpperCase() === el.toUpperCase()) {
                                return <div className={styles.errorField} key={el}>поле {el} не валидное!</div>
                            }
                        })}
                        {input(el, 'textarea',false)}
                    </div>
                )
            })}

            <div><input className={styles.checkS} type="submit"/></div>

        </form>

    )
}

export default PersonDataEdit
