import MainContentUtilit from "../Utilit/MainContentUtilit";

const CREATEPOSTMAIN = 'createPost'
const SETPOFILE = 'SETPOFILE'
const SETSTATUS = 'SETSTATUS'
const DELETPOST = 'DELETPOST'
const LOADAVATAR = 'LOADAVATAR'
const FETCHAWATAR = 'FETCHAWATAR'
const SETID = 'SETID'
const SETERROREDIT = 'SETERROREDIT'

export const ActionCreatePostMainPage = (post) => {
    return {
        type: CREATEPOSTMAIN,
        post
    }
}
export const IsFetchingAvatar = (bol) => {
    return {
        type: FETCHAWATAR,
        bol
    }
}
export const ActionCreateLoadAvatar = (photo) => {
    return {
        type: LOADAVATAR,
        photo
    }
}
export const DeletePost = (id) => {
    return {
        type: DELETPOST,
        id
    }
}
export const SetId = (id) => {
    return {
        type: SETID,
        id
    }
}

export const SetStatus = (statusText) => {
    return {
        type: SETSTATUS,
        statusText
    }
}
export const SetProfile = (Profile) => {
    return {
        type: SETPOFILE,
        Profile
    }
}
export const SetErrorEdit = (error) => {
    return {
        type: SETERROREDIT,
        error
    }
}

const initialState = {

    errorsEdit: [],
    arrayPost: [
        {
            id: '',
            name: '',
            img: '',
            massage: 'Hello',
            likes: '10'
        },
    ],
    areaText: '',
    userId: '',
    status: '',
    isFetchingAvatar: false,
    profile: {
        fullName: '',
        aboutMe: 'me',
        contacts: {
            facebook: 'dsdsd',
            github: '',
            instagram: '',
            mainLink: '',
            twitter: '',
            vk: '',
            website: '',
            youtube: ''
        },
        lookingForAJob: '',
        lookingForAJobDescription: '',
        photos: {
            large: "",
            small: '',
        },
    }

}


const MainContentPageReducer = (state = initialState, action) => {
    switch (action.type) {
        case CREATEPOSTMAIN:
            return {
                ...state,
                arrayPost: [...state.arrayPost, {
                    id: 233,
                    name: 'Wolf',
                    massage: action.post,
                    img: 'https://img.freepik.com/free-vector/hand-drawn-business-wolf-poster_1284-9769.jpg?t=st=1652789993~exp=1652790593~hmac=ae8c5842165c130ce589365f6d6f33b597a8465175838760dd3012618abb2686&w=360',
                    likes: 0
                }],
            }
        case SETID:

            return {
                ...state,
                userId: action.id

            }
        case FETCHAWATAR:

            return {
                ...state,
                isFetchingAvatar: action.bol

            }
        case DELETPOST:
            return {
                ...state,
                arrayPost: state.arrayPost.filter(el => el.id !== action.id)

            }
        case LOADAVATAR:
            return {
                ...state,
                profile: {
                    ...state.profile,
                    photos: action.photo
                }

            }
        case SETSTATUS:
            return {
                ...state,
                status: action.statusText

            }
        case SETERROREDIT:
            return {
                ...state,
                errorsEdit: action.error

            }
        case SETPOFILE:
            return {
                ...state,
                profile: {
                    ...state.profile,
                    fullName: action.Profile.fullName,
                    aboutMe: action.Profile.aboutMe,
                    contacts: {
                        facebook: action.Profile.contacts.facebook,
                        github: action.Profile.contacts.github,
                        instagram: action.Profile.contacts.instagram,
                        mainLink: action.Profile.contacts.mainLink,
                        twitter: action.Profile.contacts.twitter,
                        vk: action.Profile.contacts.vk,
                        website: action.Profile.contacts.website,
                        youtube: action.Profile.contacts.youtube
                    },
                    lookingForAJobDescription: action.Profile.lookingForAJobDescription,
                    lookingForAJob: action.Profile.lookingForAJob,
                    photos: {
                        large: action.Profile.photos ? action.Profile.photos.large :
                            state.profile.photos.large,
                        small: action.Profile.photos ? action.Profile.photos.small :
                            state.profile.photos.small
                    },

                }

            }
        default:
            return state
    }


}

export const GetProfileThunk = (userId) => {
    return (dispatch) => {

        MainContentUtilit.GetUserProfile(userId)
            .then(data => {
                    dispatch(SetProfile(data))
                    dispatch(SetId(data.userId))
                }
            )
    }
}
export const GetStatusThunk = (userId) => {
    return (dispatch) => {

        MainContentUtilit.GetStatus(userId).then(response => {
            dispatch(SetStatus(response.data))
        })

    }
}
export const UpdateAvatar = (userId) => {
    return (dispatch) => {
        dispatch(IsFetchingAvatar(true))

        MainContentUtilit.LoadAvatar(userId).then(response => {
            if (response.data.resultCode === 0) {
                dispatch(ActionCreateLoadAvatar(response.data.data.photos))
                dispatch(IsFetchingAvatar(false))
            }
        })

    }
}
export const UpdateStatusThunk = (status) => {
    return (dispatch) => {
        MainContentUtilit.ChangeStatus(status).then(
            data => {
                if (data.data.resultCode === 0) {
                    dispatch(SetStatus(status))
                }
            }
        )


    }
}
export const SaveProfile = (status) => {
    return async (dispatch) => {

        const response = await MainContentUtilit.SaveProfileApi(status)
        if (response.data.resultCode === 0) {
            dispatch(SetProfile(status))
            dispatch(SetErrorEdit([]))

        } else {

            dispatch(SetErrorEdit( response.data.messages.map((el) => {
                return   el.split('>').pop().split(')').shift()
            })))
            return Promise.reject(response.data.messages)

        }


    }
}
export default MainContentPageReducer