import React from 'react'
import MainContent from "./MainContent";
import {connect} from "react-redux";
import {
    ActionCreatePostMainPage,
    GetProfileThunk, GetStatusThunk, SaveProfile, UpdateAvatar, UpdateStatusThunk,
} from "../../redax/MainContentPageReducer";
import withAutirization from "../../HOC/WithAutirizationRedirect";
import {compose} from "redux";
import TakeParams from "../../HOC/TakeParams";

class MainContainerClass extends React.Component {
    lodingPropfile() {
        let userId = this.props.param['*']
        if (!userId) {
            userId = this.props.idProfile
        }
        this.props.GetProfileThunk(userId)

        this.props.GetStatusThunk(userId)
    }

    componentDidMount() {
        this.lodingPropfile()
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.param['*'] !== this.props.param['*']) {
            this.lodingPropfile()
        }
    }

    render() {
        return <MainContent data={this.props}/>

    }

}

const mapStateToProps = (state) => {

    return {
        arrayPost: state.MainContentPage.arrayPost,
        textCurrentValue: state.MainContentPage.areaText,
        status: state.MainContentPage.status,
        idProfile: state.Auth.id,
        isFetchingAvatar: state.MainContentPage.isFetchingAvatar,
        profile:state.MainContentPage.profile,
        errorEdit:state.MainContentPage.errorsEdit
    }
}


export default compose(
    TakeParams,
    withAutirization,
    connect(mapStateToProps, {
        SaveProfile,
        UpdateAvatar,
        ActionCreatePostMainPage,
        GetProfileThunk,
        GetStatusThunk,
        UpdateStatusThunk
    }),
)(MainContainerClass)

