import React from "react"
import styles from "./PersonData.module.css"
import StatusFunc from "./StatusFunction";


const PersonData = ({
                        profile,
                        status,
                        UpdateStatusThunk,
                        lookingForAJob,
                        SetEdit,
                    }) => {
    
    const isOwner = true
    return (
        <div className={styles.info_person}>
            {isOwner && <div>
                <button onClick={()=>{
                    SetEdit(true)}
                } className={styles.change}>Refresh</button></div>}
            <div className={styles.fio_person}>
              Имя:  {profile.fullName}
            </div>
            <div>
                <StatusFunc status={status} UpdateStatusThunk={UpdateStatusThunk}/>
            </div>
            <div>
                О бо мне: {profile.aboutMe}
            </div>

            <div>
                <h2>
                    Контакты:
                </h2>
                {Object.keys(profile.contacts).map((el) => {
                    return (
                        <div className={styles.contactItem} key={el}>{el} :{profile.contacts[el]}</div>
                    )
                })}
            </div>
            <div>
                В поиске работы? : {profile.lookingForAJob ? 'yes' : 'no'}
            </div>
            <div>
                Мои навыки :{profile.lookingForAJobDescription}
            </div>


        </div>

    )
}

export default PersonData
