import React, {useState} from "react"
import styles from "./infoPerson.module.css"
import PersonData from "./PersonData/PersonData"
import PersonDataEdit from "./PersonDataEdit/PersonDataEdit";


const InfoPerson = (props) => {
    const [editMode, SetEdit] = useState(false)

    return (
        <div className={styles.info_person}>
            {editMode ? <PersonDataEdit
                    profile={props.profile}
                    SaveProfile={props.SaveProfile}
                    errorEdit={props.errorEdit}
                    SetEdit={SetEdit}/>
            : <PersonData status={props.status}
                             UpdateStatusThunk={props.UpdateStatusThunk}
                             profile={props.profile}
                             SetEdit={SetEdit}
            />}

        </div>

    )
}

export default InfoPerson
