import React from "react"
import styles from './MainContent.module.css'
import Logoinfo from "./LogoInfo/logoInfo"
import MyPosts from "./MyPosts/MyPosts";
import {SaveProfile, UpdateAvatar} from "../../redax/MainContentPageReducer";



const MainContent = (props) => {
    return (
       <div>
           <div className={styles.body__info}>
               <div className={styles.logoInfo}>
                   <Logoinfo
                             status={props.data.status}
                             UpdateStatusThunk={props.data.UpdateStatusThunk}
                             UpdateAvatar={props.data.UpdateAvatar}
                             isFetchingAvatar={props.data.isFetchingAvatar}
                             profile={props.data.profile}
                             SaveProfile={props.data.SaveProfile}
                             errorEdit={props.data.errorEdit}
                   />
               </div>
               <MyPosts arrayPost={props.data.arrayPost}
                        createPost={props.data.ActionCreatePostMainPage}
                        photo={props.data.profile.photos}
                        fullName={props.data.fullName}/>
           </div>
       </div>

    )
}

export default MainContent
