import React from "react"

import InfoPerson from "./InfoPerson/infoPerson"
import Avatar from "./Avatar/Avatar"
import styles from './LogoInfo.module.css'

const Logoinfo = (props) => {
    return (
        <div className={styles.ava_info}>
            <Avatar avatar={props.profile.photos.large}
                    UpdateAvatar={props.UpdateAvatar}
                    isFetchingAvatar={props.isFetchingAvatar}

            />
            <InfoPerson
                        status={props.status}
                        UpdateStatusThunk={props.UpdateStatusThunk}
                        profile={props.profile}
                        SaveProfile={props.SaveProfile}
                        errorEdit={props.errorEdit}
                      />
        </div>
    )
}

export default Logoinfo
