import React from "react"
import styles from './Avatar.module.css'
import defaultAvata from '../../../../assets/img/avataWolf.jpg'
import LoaderCircle from "../../../../loder";



const Avatar = (props) => {


    const loadAvatar = (e) => {
        if (e.target.files.length > 0) {
            props.UpdateAvatar(e.target.files[0])
        }
    }
    const isOwner = true
    return (

        <div className={styles.ava_block}>
            {!props.isFetchingAvatar?
                <img className={styles.avatar} src={props.avatar ? props.avatar : defaultAvata} alt=""/>:
                <LoaderCircle/>}

            {isOwner && <div>
                <label className={!props.isFetchingAvatar?styles.labelPhoto:styles.loaded } htmlFor="Photo">извенить аватар
                    <input onChange={loadAvatar} id={'Photo'} name={'Photo'} type="file"/>
                </label>
            </div>}
        </div>

    )
}

export default Avatar
